import React, { Component} from 'react';
import { TextMessage } from './TextMessage';

export class OutcomingTextMessage extends Component {
    render() {
        return (
            <div className="message__outcoming message">
                {
                    this.props.sender?
                        <div className="message__sender">
                            {this.props.sender}
                        </div>
                        :
                        <div></div>
                }


                <TextMessage text={this.props.text} />
            </div>
        )
    }
}