import React, { Component } from 'react';

export class Factoid extends Component {
    render() {
        return (
            <div className="factoid">
                <div className="factoid__number">
                    {this.props.number}
                </div>

                <img className="factoid__picture" src={"/images/" + this.props.path}/>
                
                <div className="factoid__description">
                    {this.props.description}
                </div>
            </div>
        )
    }
}