import React, { Component } from 'react';

export class ButtonLink extends Component {

    render() {
        let class_name = "button_link";
        class_name += this.props.margin? " button_link__margin" : "";
        class_name += this.props.chosen? " button_link__chosen" : "";
        class_name += this.props.margin_left? " button_link__margin_left" : "";
        class_name += this.props.fullpage? " button_link__fullpage" : "";
        class_name += this.props.top13? " button_link__top13" : "";

        return (
            <div className={class_name} id={this.props.id}>
                {this.props.text}
            </div>
        );
    }
}