import './App.css';
import React, { Component } from 'react';
import {ContactsScreen} from "./components/screens/ContactsScreen";
import {RoadmapScreen} from "./components/screens/RoadmapScreen";
import {EmailScreen} from "./components/screens/EmailScreen";
import {HowItWorksScreen} from "./components/screens/HowItWorksScreen";
import {IntroScreen} from "./components/screens/IntroScreen";
import {UsecasesScrollingScreen} from "./components/screens/UsecasesScrollingScreen";
import {AboutDeviceScreen} from "./components/screens/AboutDeviceScreen";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        let handleStateUpdate  = this.handleStateUpdate.bind(this);
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate  = this.handleStateUpdate;

        return (
            <div className="app">
                <IntroScreen handle={handleStateUpdate.bind(this)}/>
                <AboutDeviceScreen />
                <HowItWorksScreen />
                <UsecasesScrollingScreen />
                <EmailScreen />
                <RoadmapScreen />
                <ContactsScreen />
            </div>
        );
    }
}

export default App;
