import React, { Component} from 'react';
import {MailInput} from "../interactive/MailInput";
import {Category} from "../Category";
import strings from "../Localisation";
import {IncomingTextMessage} from "../messages/TextMessages/IncomingTextMessage";
import idGenerator from 'react-id-generator';
import axios from 'axios';
import {ButtonLink} from "../interactive/ButtonLink";
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import cookie from 'react-cookies'
import {ContactLinksMessage} from "../messages/LinkMessages/ContactLinksMessage";
import {EmailLinksMessage} from "../messages/LinkMessages/EmailLinksMessage";

export class EmailScreen extends Component {
    constructor(props) {
        super(props);

        this.buttonId = idGenerator();
        this.inputId = idGenerator();

        let email_who = cookie.load("email_who");
        let email_success = cookie.load("email_success");

        this.state = {
            user_email__text: email_who? email_who : "",
            sending_success__text: email_success? email_success : ""
        }
    }

    sendRequest(event, link) {
        const language = strings.getLanguage();
        const mail = document.getElementById(link.inputId).value.replace("\ \g", "");

        if (mail.length > 0 && language.length > 0) {
            axios.get("https://maker.ifttt.com/trigger/email_sent/with/key/dZ2N1BVOi6c5zCIOD27V9Q?value1=" + mail  + "&value2=" + language)
                .then(function (response) {
                    link.handleResponse(mail);
                })
                .catch(function (error) {
                    link.handleResponse(mail);
                    console.log(error);
                });
        }
    }

    handleResponse(mail) {
        cookie.save("email_who", strings.email_form__user_sent + mail);
        cookie.save("email_success", strings.email_form__success_toast);

        this.setState({
            user_email__text: strings.email_form__user_sent + mail,
            sending_success__text: strings.email_form__success_toast
        });
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let input = document.getElementById(this.inputId);

        const link = this;

        input.addEventListener('submit', function(e) {
            console.log(e);
            e.preventDefault();
            button.click();
        });

        input.addEventListener('keypress', function (e) {
            let key = e.key || e.altKey;
            console.log(key);

            if (key === 'Enter') {
                e.preventDefault();
                button.click();
            }
        });

        button.addEventListener("click", function(event) {
            event.preventDefault();
            link.sendRequest(event, link);
        });
    }

    handleKeyPress(event) {
        event.preventDefault();
        document.getElementById(this.buttonId).click();
    }

    verifyComponent(component) {
        return this.state.user_email__text.length > 0? component : <div></div>;
    }

    render() {
        return (
            <Category
                id="category__email"
                title={strings.email_form__title}
                subtitle={strings.email_form__subtitle}>

                <IncomingTextMessage
                    sender={strings.developer}
                    text={strings.email_form__developer} />

                {
                    this.verifyComponent(
                        <OutcomingTextMessage
                            sender={strings.user}
                            text={this.state.user_email__text} />
                    )
                }

                {
                    this.verifyComponent(
                        <IncomingTextMessage
                            text={this.state.sending_success__text} />
                    )
                }

                {
                    this.verifyComponent(
                        <IncomingTextMessage
                            text={strings.email_form__survey_dev__text} />
                    )
                }

                {
                    this.verifyComponent(
                        <EmailLinksMessage
                            link={strings.email_form__survey_dev__link} />
                    )
                }

                <div className="email_form">
                    <MailInput placeholder={strings.email_form__placeholder} onClick={this.handleKeyPress} id={this.inputId} />
                    <ButtonLink text={strings.email_form__button} onClick={this.handleKeyPress} id={this.buttonId} />
                </div>
            </Category>
        )
    }
}