import React, { Component} from 'react';
import strings from '../Localisation';
import { Category } from '../Category';
import {FactoidGreed} from "../factoids/FactoidGreed";
import {Factoid} from "../factoids/Factoid";
import {ButtonLink} from "../interactive/ButtonLink";
import {GridButtonLink} from "../interactive/GridButtonLink";
import idGenerator from "react-id-generator";
import cookie from 'react-cookies'

export class HowItWorksScreen extends Component {
    constructor(props) {
        super(props);

        this.button_id__dev = idGenerator();
        this.button_id__user = idGenerator();

        let howitworks_who = cookie.load("howitworks_who");

        this.state = { chosen: howitworks_who? howitworks_who : "user" };
    }

    componentDidMount() {
        let button_user =  document.getElementById(this.button_id__user);
        let button_dev =  document.getElementById(this.button_id__dev);
        let link = this;

        button_user.addEventListener("click", function(event) {
            event.preventDefault();
            link.choseGuy("user");
        });

        button_dev.addEventListener("click", function(event) {
            event.preventDefault();
            link.choseGuy("dev");
        });
    }

    choseGuy(who) {
        console.log(who);
        cookie.save("howitworks_who", who);
        this.setState({chosen: who});
    }

    render() {
        return (
            <Category
                title={strings.how_it_works__user__title}>

                <GridButtonLink>
                    <ButtonLink
                        margin={true}
                        chosen={this.state.chosen === "user"}
                        id={this.button_id__user}
                        text={strings.grid_button__for_user}/>

                    <ButtonLink
                        margin={true}
                        chosen={this.state.chosen === "dev"}
                        id={this.button_id__dev}
                        text={strings.grid_button__for_developer} />

                </GridButtonLink>

                {
                    this.state.chosen === "user"?
                        <FactoidGreed>
                            <Factoid
                                number={"1"}
                                description={strings.how_it_works__user__factoid__first}
                                path={"brain.svg"}/>

                            <Factoid
                                number={"2"}
                                description={strings.how_it_works__user__factoid__second}
                                path={"ear.svg"}/>

                            <Factoid
                                number={"3"}
                                description={strings.how_it_works__user__factoid__third}
                                path={"wave.svg"}/>

                        </FactoidGreed>
                            :
                        <FactoidGreed>
                            <Factoid
                                number={"1"}
                                description={strings.how_it_works__developer__factoid__first}
                                path={"brain.svg"}/>

                            <Factoid
                                number={"2"}
                                description={strings.how_it_works__developer__factoid__second}
                                path={"ear.svg"}/>

                            <Factoid
                                number={"3"}
                                description={strings.how_it_works__developer__factoid__third}
                                path={"wave.svg"}/>

                        </FactoidGreed>
                }

            </Category>
        )
    }
}