import React, { Component} from 'react';
import strings from '../Localisation';
import { Category } from '../Category';
import {FactoidGreed} from "../factoids/FactoidGreed";
import {Factoid} from "../factoids/Factoid";
import {GridButtonLink} from "../interactive/GridButtonLink";
import {ButtonLink} from "../interactive/ButtonLink";
import idGenerator from "react-id-generator";
import cookie from 'react-cookies'

export class RoadmapScreen extends Component {
    constructor(props) {
        super(props);

        this.button_id__dev = idGenerator();
        this.button_id__user = idGenerator();

        let roadmap_who = cookie.load("roadmap_who");

        this.state = { chosen: roadmap_who? roadmap_who : "user" };
    }

    componentDidMount() {
        let button_user =  document.getElementById(this.button_id__user);
        let button_dev =  document.getElementById(this.button_id__dev);
        let link = this;

        button_user.addEventListener("click", function(event) {
            event.preventDefault();
            link.choseGuy("user");
        });

        button_dev.addEventListener("click", function(event) {
            event.preventDefault();
            link.choseGuy("dev");
        });
    }

    choseGuy(who) {
        console.log(who);
        cookie.save("roadmap_who", who);
        this.setState({chosen: who});
    }

    render() {
        return (
            <Category
                title={strings.roadmap__title}>

                <GridButtonLink>
                    <ButtonLink
                        margin={true}
                        chosen={this.state.chosen === "user"}
                        id={this.button_id__user}
                        text={strings.grid_button__for_user}/>

                    <ButtonLink
                        margin={true}
                        chosen={this.state.chosen === "dev"}
                        id={this.button_id__dev}
                        text={strings.grid_button__for_developer} />

                </GridButtonLink>

                <FactoidGreed>
                    <Factoid
                        number={"1"}
                        description={strings.roadmap_factoid__user__first}
                        path={"binary.svg"}/>

                    <Factoid
                        number={"2"}
                        description={strings.roadmap_factoid__user__second}
                        path={"ear.svg"}/>

                    <Factoid
                        number={"3"}
                        description={strings.roadmap_factoid__user__third}
                        path={"dollar.svg"}/>

                </FactoidGreed>
            </Category>
        )
    }
}