import React, { Component } from 'react';
import {VideoMessage} from "./VideoMessage";

export class IncomingVideoMessage extends Component {

    render() {
        return (
            <div className="message__incoming message">
                {
                    this.props.sender?
                        <div className="message__sender">
                            {this.props.sender}
                        </div>
                        :
                        <div></div>
                }


                <VideoMessage path={this.props.path}/>
            </div>
        );
    }
}