import React, { Component } from 'react';
import { PhotoMessage } from './PhotoMessage';

export class OutcomingPhotoMessage extends Component {
    render() {
        return (
            <div className="message__outcoming message">
                {
                    this.props.sender?
                        <div className="message__sender">
                            {this.props.sender}
                        </div>
                        :
                        <div></div>
                }

                <PhotoMessage path={this.props.path}/>
            </div>
        )
    }
}